export const isFn=v=>typeof v==='function';export const isStr=v=>typeof v==='string';export const isObj=v=>v&&typeof v==='object'&&!Array.isArray(v);export const deepMerge=(a={},b={})=>{const o={...a};for(const k of Object.keys(b)){const av=a[k],bv=b[k];o[k]=(isObj(av)&&isObj(bv))?deepMerge(av,bv):bv;}return o;};export const debounce=(fn,ms=250)=>{let t;return(...args)=>{clearTimeout(t);t=setTimeout(()=>fn(...args),ms);};};export const backoff=(i,base=200)=>new Promise(r=>setTimeout(r,base*(2**i)));