export class SEOManager{constructor(d=document){this.doc=d}title(t){if(t!=null)this.doc.title=String(t);return this.doc.title}meta(n,c){let el=this.doc.querySelector(`meta[name="${n}"]`);if(!el){el=this.doc.createElement('meta');el.setAttribute('name',n);this.doc.head.appendChild(el)}el.setAttribute('content',c);return el}link(rel,href){let el=this.doc.querySelector(`link[rel="${rel}"]`);if(!el){el=this.doc.createElement('link');el.setAttribute('rel',rel);this.doc.head.appendChild(el)}el.setAttribute('href',href);return el}jsonLD(id,obj){let el=this.doc.getElementById(id);if(!el){el=this.doc.createElement('script');el.type='application/ld+json';el.id=id;this.doc.head.appendChild(el)}el.textContent=JSON.stringify(obj);return el}}